@import url(https://fonts.googleapis.com/css?family=Nunito+Sans|Roboto+Mono&display=swap);
.header{font-family:'Roboto Mono', monospace;font-size:2.5rem;text-align:center;padding:1rem;margin:1rem}

.search{font-family:'Nunito Sans', sans-serif;text-align:center;color:#fff;max-width:400px;margin:0 auto;padding:1rem}.search__group{display:flex}.search__group-input,.search__group-submit{border:1px transparent solid;padding:.5rem;font-size:1.6rem;outline:none}.search__group-input{flex:1 1}.search__group-submit{padding:.5rem 2rem;background:#465668;color:#fff;width:15%;cursor:pointer}.search__clear-btn{display:block;width:100%;font-size:1.6rem;border:1px transparent solid;padding:.5rem;margin-top:1rem;background-color:#a8b2d1;color:#333f58;cursor:pointer}.search__clear-btn:hover{background-color:#ccd6f6}

.card{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;min-height:153px;background-color:#172a45;padding:1rem;overflow:hidden;cursor:pointer;text-decoration:none}.card__img{width:70px;height:auto;border-radius:50%}.card__user{color:#e6f1ff}.card__read-more{color:#8892b0}.card:hover{-webkit-transform:scale(1.03);transform:scale(1.03);-webkit-transition:0.3s ease-out;transition:0.3s ease-out}.card:hover .card__user{color:#64ffda}.card:hover .card__read-more{color:#e6f1ff}

.loading-spinner{width:100px;margin:0 auto;display:block;margin-top:10rem}

.card-list{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:1rem;gap:1rem;margin:5rem 1.5rem}

.user{max-width:1100px;margin:0 auto}.user__link{padding:0 1rem;text-decoration:none;color:#637aac;margin-bottom:1rem;display:inline-block}.user__link:hover{color:#496092}.user__link::after{content:"|"}.user__card{display:grid;grid-template-columns:1fr 1fr;padding:0 1rem;background:#172a45}.user__card-avatar,.user__card-info{padding:1rem;min-height:300px;display:flex;flex-direction:column;justify-content:space-evenly}.user__card-avatar{align-items:center}.user__card-avatar-photo{max-width:200px}.user__card-info{align-items:flex-start}.user__card-info-bio{text-align:justify}.user__card-info-url{text-decoration:none;background:#01111a;color:#a8b2d1;padding:1rem}.user__card-info-url:hover{background:#01080c;color:#e6f1ff}.user__footer{margin-top:0.5rem;padding:3rem;background:#172a45;display:flex;flex-wrap:wrap;justify-content:space-evenly;align-items:center;color:#fff}.user__footer p{padding:0.5rem 1rem;margin-top:1rem;border-radius:4px}.user__footer-followers{background:#0765bd}.user__footer-following{background:#b8890a}.user__footer-repos{background:#6b7185}.user__footer-gists{background:#343a49}.text-white{color:#e6f1ff}.text-blur{color:#a8b2d1}.text-success{color:#17fc7e}.text-danger{color:#f52b2b}@media (max-width: 769px){.back-to-search{text-align:center}.user__card-info-url{font-size:1.5rem}.user__footer{display:none}.user__card{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:0;margin:0;background:none}.user__card-avatar,.user__card-info{background:#172a45;padding:1rem;margin:0.5rem;width:100%}.user__card-info{display:flex;flex-direction:column;justify-content:space-around;align-items:center}.user__card-info-bio{text-align:center}.user__card-info-url{font-size:1.6rem;width:90%;text-align:center}}

.about{text-align:center;margin:2rem}.about h1{color:#e6f1ff}.about p{color:#a8b2d1}.about__link{color:#317a94;text-decoration:none;margin-top:2rem;display:inline-block}.about__link:hover{color:#3cbbcc;-webkit-transition:0.2s ease-out;transition:0.2s ease-out}

*{margin:0;padding:0}*::before,*::after{box-sizing:inherit}html{font-size:62.5%}body{font-family:"Roboto Mono",monospace;font-size:1.6rem;line-height:1.6;box-sizing:border-box;background-color:#0a192f;color:#64ffda;min-height:100vh}.container{max-width:1270px;margin:0 auto}@media (max-width: 1024px){.card-list{grid-template-columns:repeat(3, 1fr)}.header{font-size:2.3rem}}@media (max-width: 768px){.card-list{grid-template-columns:repeat(2, 1fr)}.header{font-size:2rem}}@media (max-width: 500px){.card-list{grid-template-columns:1fr}}

